import React from "react";
import styled from "styled-components";

const TitleText = styled.span`
  font-size: 32px;
  font-weight: 800;
`;

const Title = ({ children }) => <TitleText>{children}</TitleText>;

const Text0 = styled.h2`
  font-size: 14px;
  font-weight: 700;
  line-height: 1.5;
  opacity: ${(props) => props.opacity};
`;

const T0 = ({ children, opacity }) => {
  return <Text0 opacity={opacity}>{children}</Text0>;
};

const Text1 = styled.span`
  font-size: 12px;
  opacity: ${(props) => props.opacity};
  line-height: 1.5;
`;

const T1 = ({ children, opacity }) => {
  return <Text1 opacity={opacity}>{children}</Text1>;
};

const Text2 = styled.span`
  font-size: 10px;
  opacity: ${(props) => props.opacity};
  line-height: 1.5;
`;

const T2 = ({ children, opacity }) => {
  return <Text2 opacity={opacity}>{children}</Text2>;
};

export { Title, T0, T1, T2 };
