import React, { useState } from "react";
import styled from "styled-components";
import { T0, T1, T2 } from "./Typo";
import { Margin } from "./Utility";
import instagram from "../Assets/Images/Icons/instagram.png";
import youtube from "../Assets/Images/Icons/youtube.png";
import { deleteDocument } from "../Firebase/DbFunc";
import PopUp from "./PopUp";
import ContentsUpdate from "./ContentsUpdate";

const Box = styled.div`
  display: flex;
  width: 100%;
  flex-direction: column;
`;

const TopBox = styled.div`
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  align-items: center;
  padding: 5px;
`;

const UtilBox = styled.div``;

const UtilBtn = styled.button`
  font-size: 10px;
  padding: 5px;
  cursor: pointer;
  margin-right: 20px;
  &:last-child {
    margin-right: 0px;
  }
`;

const CardBox = styled.div`
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  border-width: 1px;
  border-style: solid;
  border-radius: 10px;
  overflow: hidden;
  &:hover {
    -webkit-box-shadow: 0 1px 6px 0 rgba(32, 33, 36, 0.28);
    -moz-box-shadow: 0 1px 6px 0 rgba(32, 33, 36, 0.28);
    box-shadow: 0 1px 6px 0 rgba(32, 33, 36, 0.28);
    top: -3px;
  }
`;

const Playtime = styled.div`
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  right: 10px;
  bottom: 10px;
  color: rgba(255, 255, 255, 1);
  padding: 3px;
  text-align: right;
`;

const Thumbnail = styled.div`
  padding-top: 56.25%;
  background-color: red;
  position: relative;
  background: ${(props) =>
    `rgba(0,0,0,1) url(${props.src}) no-repeat center center`};
  background-size: cover;
  border-bottom-width: 1px;
  border-bottom-style: solid;
`;

const DescBox = styled.div`
  display: flex;
  width: 100%;
  flex-direction: column;
  padding: 15px;
`;

const EditorsBox = styled.div`
  margin-top: 15px;
  padding: 10px 15px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  border-width: 1px;
  border-style: solid;
`;

const ChannelBox = styled.div`
  border-top-width: 1px;
  border-top-style: solid;
  padding: 0px 15px;
  height: 40px;
  display: flex;
  align-items: center;
`;

const ChannelIcon = styled.img`
  width: 20px;
  margin-right: 10px;
`;

const ContentsCard = ({ metadata, add }) => {
  const [update, setUpdate] = useState(false);
  const channelIconSrc = {
    youtube: youtube,
    instagram: instagram,
  };
  const deleteUrl = () => {
    const confirm = window.confirm("  ?");
    if (confirm) {
      deleteDocument("urls", metadata.id);
    } else {
      return;
    }
  };

  return (
    <>
      <Box>
        <TopBox>
          <T0> : {metadata.category}</T0>
          {add ? null : (
            <UtilBox>
              <UtilBtn onClick={() => setUpdate(true)}></UtilBtn>
              <UtilBtn onClick={deleteUrl}></UtilBtn>
            </UtilBox>
          )}
        </TopBox>
        <CardBox>
          <Thumbnail src={metadata.image}>
            {metadata.time ? (
              <Playtime>
                <T2>{metadata.time}</T2>
              </Playtime>
            ) : null}
          </Thumbnail>
          <DescBox>
            <T0>{metadata.title}</T0>
            <Margin height={8}></Margin>
            <T1>
              {metadata.description.length > 70
                ? `${metadata.description.substr(0, 70)}...`
                : metadata.description}
            </T1>
            {metadata.editorsPick ? (
              <EditorsBox>
                <T1>Editor's Pick</T1>
                <Margin height={3}></Margin>
                <T2>{metadata.editorsPickReason}</T2>
              </EditorsBox>
            ) : null}
          </DescBox>
          <ChannelBox>
            <ChannelIcon src={channelIconSrc[metadata.channel]}></ChannelIcon>
            <T2>{metadata.author}</T2>
          </ChannelBox>
        </CardBox>
      </Box>
      {update ? (
        <PopUp closePopUp={() => setUpdate(false)}>
          <ContentsUpdate
            metadata={metadata}
            closePopUp={() => setUpdate(false)}
          ></ContentsUpdate>
        </PopUp>
      ) : null}
    </>
  );
};

export default ContentsCard;
