import React, { useState } from "react";
import styled from "styled-components";
import { addDocument } from "../Firebase/DbFunc";
import { getMetadata } from "../Functions/Functions";
import ContentsCard from "./ContentsCard";
import { T1 } from "./Typo";
import { Margin } from "./Utility";

const Box = styled.div`
  width: 100%;
  display: flex;
  margin-top: 20px;
  margin-bottom: 40px;
`;

const LeftBox = styled.div`
  display: flex;
  flex-direction: column;
  margin-right: 30px;
`;

const FormBox = styled.form`
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
`;

const TextInput = styled.input`
  margin-right: 10px;
  width: 200px;
  margin-bottom: 10px;
`;

const SubmitInput = styled.input`
  width: 200px;
  cursor: pointer;
`;

const RadioLabel = styled.label`
  margin-right: 10px;
`;

const RadioInput = styled.input``;

const ResultBox = styled.div`
  display: grid;
  width: 300px;
  margin-bottom: 20px;
`;

const ContentsAdd = () => {
  const [url, setUrl] = useState("");
  const [metadata, setMetadata] = useState(null);

  const setDefaultMeta = (value) => {
    setMetadata({ ...value, channel: "youtube", createdAt: new Date() });
  };

  const onUrlChange = (event) => {
    const value = event.target.value;
    setUrl(value);
  };

  const updateMetadata = (event, type) => {
    event.preventDefault();
    const value = event.target.value;
    if (type === "category") {
      setMetadata({ ...metadata, category: value });
    } else if (type === "time") {
      setMetadata({ ...metadata, time: value });
    } else if (type === "author") {
      setMetadata({ ...metadata, author: value });
    } else if (type === "channel") {
      setMetadata({ ...metadata, channel: value });
    } else if (type === "title") {
      setMetadata({ ...metadata, title: value });
    } else if (type === "description") {
      setMetadata({ ...metadata, description: value });
    } else if (type === "image") {
      setMetadata({ ...metadata, image: value });
    }
  };

  const onSearch = async (event) => {
    event.preventDefault();
    const valid = url.length > 0;
    if (valid) {
      getMetadata(url, setDefaultMeta);
    } else {
      return;
    }
  };

  const onUpload = async () => {
    addDocument("urls", metadata);
    setUrl("");
  };

  return (
    <Box>
      <LeftBox>
        <FormBox onSubmit={onSearch}>
          <TextInput
            type="text"
            placeholder="URL "
            value={url}
            onChange={onUrlChange}
          ></TextInput>
          <SubmitInput
            type="submit"
            value=""
            valid={url.length > 0}
          ></SubmitInput>
        </FormBox>
        {metadata ? (
          <>
            <FormBox onSubmit={onUpload}>
              <T1></T1>
              <TextInput
                type="text"
                placeholder=" "
                value={metadata.category}
                onChange={(event) => updateMetadata(event, "category")}
              ></TextInput>
              <T1> </T1>
              <TextInput
                type="text"
                placeholder="  "
                value={metadata.image}
                onChange={(event) => updateMetadata(event, "image")}
              ></TextInput>
              <T1></T1>
              <TextInput
                type="text"
                placeholder=" "
                value={metadata.time}
                onChange={(event) => updateMetadata(event, "time")}
              ></TextInput>
              <T1></T1>
              <TextInput
                type="text"
                placeholder=" "
                value={metadata.title}
                onChange={(event) => updateMetadata(event, "title")}
              ></TextInput>
              <T1></T1>
              <TextInput
                type="textarea"
                placeholder=" "
                value={metadata.description}
                onChange={(event) => updateMetadata(event, "description")}
              ></TextInput>
              <T1></T1>
              <TextInput
                type="text"
                placeholder=" "
                value={metadata.author}
                onChange={(event) => updateMetadata(event, "author")}
              ></TextInput>
              <T1></T1>
              <RadioLabel>
                <RadioInput
                  type="radio"
                  value="youtube"
                  onChange={(event) => updateMetadata(event, "channel")}
                  checked={metadata.channel === "youtube"}
                ></RadioInput>
                
              </RadioLabel>
              <RadioLabel>
                <RadioInput
                  type="radio"
                  value="etc"
                  onChange={(event) => updateMetadata(event, "channel")}
                  checked={metadata.channel === "etc"}
                ></RadioInput>
                
              </RadioLabel>
              <Margin height={15}></Margin>
              {metadata ? (
                <SubmitInput type="submit" value=""></SubmitInput>
              ) : null}
            </FormBox>
          </>
        ) : null}
      </LeftBox>
      <ResultBox>
        {metadata ? (
          <ContentsCard metadata={metadata} add={true}></ContentsCard>
        ) : null}
      </ResultBox>
    </Box>
  );
};

export default ContentsAdd;
