import React, { useState } from "react";
import styled from "styled-components";
import { logIn } from "../Firebase/AuthFunc";

const Box = styled.div`
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const Label = styled.label`
  margin-bottom: 5px;
`;

const LoginForm = styled.form`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;

const IdInput = styled.input`
  margin-bottom: 20px;
`;

const PwInput = styled.input`
  margin-bottom: 20px;
`;

const Submit = styled.input``;

const Login = () => {
  const [email, setEmail] = useState("");
  const [pw, setPw] = useState("");

  const onTextChange = (event, email) => {
    const value = event.target.value;
    if (email) {
      setEmail(value);
    } else {
      setPw(value);
    }
  };

  const onSubmit = (event) => {
    event.preventDefault();
    logIn(email, pw);
  };

  return (
    <Box>
      <LoginForm onSubmit={onSubmit}>
        <Label></Label>
        <IdInput
          type="text"
          value={email}
          placeholder=" "
          onChange={(event) => onTextChange(event, true)}
        ></IdInput>
        <Label></Label>
        <PwInput
          type="password"
          value={pw}
          placeholder=" "
          onChange={(event) => onTextChange(event, false)}
        ></PwInput>
        <Submit type="submit" value=""></Submit>
      </LoginForm>
    </Box>
  );
};

export default Login;
