import React, { useState } from "react";
import styled from "styled-components";
import { updateDocument } from "../Firebase/DbFunc";
import ContentsCard from "./ContentsCard";
import { T1 } from "./Typo";
import { Margin } from "./Utility";

const Box = styled.div`
  width: 100%;
  display: flex;
  margin-top: 20px;
  margin-bottom: 40px;
`;

const FormBox = styled.form`
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  margin-right: 20px;
`;

const TextInput = styled.input`
  margin-right: 10px;
  width: 200px;
  margin-bottom: 10px;
`;

const SubmitInput = styled.input`
  width: 200px;
  cursor: pointer;
`;

const RadioLabel = styled.label`
  margin-right: 10px;
`;

const RadioInput = styled.input``;

const ResultBox = styled.div`
  display: grid;
  width: 300px;
  margin-bottom: 20px;
`;

const ContentsUpdate = ({ metadata, closePopUp }) => {
  const [newMetadata, setNewMetadata] = useState(metadata);
  const updateMetadata = (event, type) => {
    event.preventDefault();
    const value = event.target.value;
    if (type === "url") {
      setNewMetadata({ ...newMetadata, url: value });
    } else if (type === "category") {
      setNewMetadata({ ...newMetadata, category: value });
    } else if (type === "time") {
      setNewMetadata({ ...newMetadata, time: value });
    } else if (type === "author") {
      setNewMetadata({ ...newMetadata, author: value });
    } else if (type === "channel") {
      setNewMetadata({ ...newMetadata, channel: value });
    } else if (type === "title") {
      setNewMetadata({ ...newMetadata, title: value });
    } else if (type === "description") {
      setNewMetadata({ ...newMetadata, description: value });
    } else if (type === "image") {
      setNewMetadata({ ...newMetadata, image: value });
    } else if (type === "editorsPick") {
      setNewMetadata((prev) => ({
        ...newMetadata,
        editorsPick: !prev.editorsPick,
      }));
    } else if (type === "editorsPickReason") {
      setNewMetadata({ ...newMetadata, editorsPickReason: value });
    }
  };

  const onUpdate = async (event) => {
    event.preventDefault();
    updateDocument("urls", metadata.id, newMetadata);
    closePopUp();
  };

  return (
    <Box>
      <FormBox onSubmit={onUpdate}>
        <T1>URL</T1>
        <TextInput
          type="text"
          placeholder="url "
          value={newMetadata.url}
          onChange={(event) => updateMetadata(event, "url")}
        ></TextInput>
        <T1></T1>
        <TextInput
          type="text"
          placeholder=" "
          value={newMetadata.category}
          onChange={(event) => updateMetadata(event, "category")}
        ></TextInput>
        <T1> </T1>
        <TextInput
          type="text"
          placeholder="  "
          value={newMetadata.image}
          onChange={(event) => updateMetadata(event, "image")}
        ></TextInput>
        <T1></T1>
        <TextInput
          type="text"
          placeholder=" "
          value={newMetadata.time}
          onChange={(event) => updateMetadata(event, "time")}
        ></TextInput>
        <T1></T1>
        <TextInput
          type="text"
          placeholder=" "
          value={newMetadata.title}
          onChange={(event) => updateMetadata(event, "title")}
        ></TextInput>
        <T1></T1>
        <TextInput
          type="textarea"
          placeholder=" "
          value={newMetadata.description}
          onChange={(event) => updateMetadata(event, "description")}
        ></TextInput>
        <T1></T1>
        <TextInput
          type="text"
          placeholder=" "
          value={newMetadata.author}
          onChange={(event) => updateMetadata(event, "author")}
        ></TextInput>
        <T1></T1>
        <RadioLabel>
          <RadioInput
            type="radio"
            value="youtube"
            onChange={(event) => updateMetadata(event, "channel")}
            checked={newMetadata.channel === "youtube"}
          ></RadioInput>
          
        </RadioLabel>
        <RadioLabel>
          <RadioInput
            type="radio"
            value="etc"
            onChange={(event) => updateMetadata(event, "channel")}
            checked={newMetadata.channel === "etc"}
          ></RadioInput>
          
        </RadioLabel>
        <Margin height={15}></Margin>
        <T1>Editor's Pick</T1>
        <RadioLabel>
          <RadioInput
            type="checkbox"
            value="editorsPick"
            onChange={(event) => updateMetadata(event, "editorsPick")}
            checked={newMetadata.editorsPick === true}
          ></RadioInput>
           
        </RadioLabel>
        <Margin height={10}></Margin>
        <TextInput
          type="text"
          placeholder=" "
          value={newMetadata.editorsPickReason}
          onChange={(event) => updateMetadata(event, "editorsPickReason")}
        ></TextInput>
        <SubmitInput type="submit" value=""></SubmitInput>
      </FormBox>
      <ResultBox>
        {metadata ? (
          <ContentsCard metadata={newMetadata} add={true}></ContentsCard>
        ) : null}
      </ResultBox>
    </Box>
  );
};

export default ContentsUpdate;
