import {
  getFirestore,
  query,
  doc,
  where,
  collection,
  addDoc,
  onSnapshot,
  or,
  getDocs,
  deleteDoc,
  updateDoc,
  and,
  limit,
  orderBy,
  startAfter,
} from "firebase/firestore";

const db = getFirestore();

const addDocument = async (collectionName, documentContents) => {
  try {
    await addDoc(collection(db, collectionName), documentContents);
  } catch (e) {
    console.error("Error adding document: ", e);
  }
};

const deleteDocument = async (collectionName, documentId) => {
  const documentInfo = doc(db, collectionName, documentId);
  await deleteDoc(documentInfo);
};

const updateDocument = async (collectionName, documentId, value) => {
  const documentInfo = doc(db, collectionName, documentId);
  await updateDoc(documentInfo, {
    ...value,
  });
};

const searchDocument = async (collectionName, param, value) => {
  const q = query(collection(db, collectionName), where(param, "==", value));
  const querySnapshot = await getDocs(q);
  const docsObj = querySnapshot.docs.map((doc) => ({
    ...doc.data(),
    id: doc.id,
  }));
  return docsObj;
};

const andSearchDocument = async (
  collectionName,
  param1,
  value1,
  param2,
  value2
) => {
  const q = query(
    collection(db, collectionName),
    or(where(param1, "==", value1), where(param2, "==", value2))
  );
  const querySnapshot = await getDocs(q);
  const docsObj = querySnapshot.docs.map((doc) => ({
    ...doc.data(),
    id: doc.id,
  }));
  return docsObj;
};

const getInfiniteDocuments = async (collectionName, key) => {
  let lastVisible;
  let contents = [];
  let q;
  if (key) {
    q = query(
      collection(db, collectionName),
      orderBy("createdAt", "desc"),
      startAfter(key),
      limit(1)
    );
  } else {
    q = query(
      collection(db, collectionName),
      orderBy("createdAt", "desc"),
      limit(2)
    );
  }
  const querySnapshot = await getDocs(q);
  querySnapshot.docs.map((doc) => {
    contents.push({
      ...doc.data(),
      id: doc.id,
    });
    lastVisible = doc.data().createdAt;
  });
  return { contents, lastVisible };
};

export {
  addDocument,
  deleteDocument,
  updateDocument,
  searchDocument,
  andSearchDocument,
  getInfiniteDocuments,
};
