import React, { useState } from "react";
import Container from "../Components/Container";
import Header from "../Components/Header";
import View from "../Components/View";
import { Title } from "../Components/Typo";
import ContentsDashBoard from "../Components/ContentsDashBoard";
import ContentsAdd from "../Components/ContentsAdd";
import ContentsList from "../Components/ContentsList";

const Home = () => {
  const [stage, setStage] = useState("contents");

  return (
    <Container>
      <Header setStage={setStage}></Header>
      <View>
        {stage === "contents" ? (
          <>
            <Title> </Title>
            <ContentsDashBoard></ContentsDashBoard>
            <Title> </Title>
            <ContentsAdd></ContentsAdd>
            <Title> </Title>
            <ContentsList></ContentsList>
          </>
        ) : null}
      </View>
    </Container>
  );
};

export default Home;
